export default {
    path: '/shouquan',
    name: 'shouquan',
    component: () => import('../views/mz/mz'),
    meta: {
        title: 'UrbanRevivo',
        keepAlive: false
    },
    beforeEnter: (to, from, next) => {
        
        if(window.localStorage.getItem('WXtoken')){
            this.$router.push({name:'home'})
        }else{
            next();
        }
    },
};
